import React from "react";
import { useParams, useNavigate, Link } from "react-router-dom";
import axios from "axios";
import MyLoader from "../components/Skeleton/skeleton";
import { useDispatch, useSelector } from "react-redux";
import s from './NotFound.module.scss'

const FullPizza: React.FC = () => {

  const dispatch = useDispatch();
  const [active, setActive] = React.useState<number>(0);
  const [sizer, setActiveSize] = React.useState<number>(0);

  const { id } = useParams();
  const navigate = useNavigate();
  const [pizza, setPizza] = React.useState<{
    imageUrl: string;
    title: string;
    price: number;
    rating: number;
    sizes: number[];
    types:number[];
  }>();

  const typeNames:string[] = ["", ""];

  React.useEffect(() => {
    async function fetchPizza() {
      try {
        const { data } = await axios.get(
          "https://64dc3d26e64a8525a0f64ec7.mockapi.io/items/" + id
        );
        setPizza(data);
        console.log(data);
      } catch (error) {
        alert("   (");
        navigate("/");
      }
    }

    fetchPizza();
  }, []);
  if (!pizza) {
    return <MyLoader />;
  }

  return (
    <div className="container-big">
      <img src={pizza.imageUrl} alt="dd" />
      <div className="pizza-block-big">
        <h4 className="pizza-block__title">{pizza.title}</h4>
        <div className="pizza-block__selector">
          <p> : {pizza.rating}</p>
          <ul>
              {pizza.types.map((type, index) => (
                <li
                  key={index}
                  onClick={() => setActive(index)}
                  className={active === index ? "active" : ""}
                >
                  {typeNames[type]}
                </li>
              ))}
            </ul>
            <ul>
              {pizza.sizes.map((size, index) => (
                <li
                  key={index}
                  onClick={() => setActiveSize(index)}
                  className={sizer === index ? "active" : ""}
                >
                  {size}.
                </li>
              ))}
            </ul>
        </div>
        <div className="pizza-block__bottom">
          <div className="pizza-block__price"> {pizza.price} </div>
        </div>
        <Link to="/"><button className={s.btn}> </button></Link>
      </div>
      
    </div>
    
  );
};

export default FullPizza;
