import React from "react";
import { useDispatch, useSelector } from "react-redux";


import { setActive} from '../redux/footer/footerSlice';



const Footer:React.FC = () => {


  const active = useSelector(//@ts-ignore
   state => state.footer.active)
  const dispatch = useDispatch()

  const [hide, setHide] = React.useState(false);

  const footerRef = React.useRef <HTMLParagraphElement>(null)

  React.useEffect(()=>{
    const handleClick = (event:MouseEvent) => {
      if( footerRef.current && !event.composedPath().includes(footerRef.current)){
        setHide(false)
      }
    }
    
    document.body.addEventListener('click', handleClick );

    return()=>{
      document.body.removeEventListener('click', handleClick)
    }
  },[])

  const onClose = (index:number) => {
    dispatch(setActive(index))
    setHide(!hide)
  } 

  const list = [
    "",
    " -    ",
    "  ",
  ];

  return (
    <div className="pizza-footer">
      <span>by WebbyWinges</span>
      <div className="block" >
        <p ref={footerRef} onClick={() => setHide(!hide)}> ...</p>
        {hide && (
          <div className="block__me" >
            {list.map((el, index) => (
              <li onClick={() => onClose(index)} key={index} className={active === index ? 'active' : '' }>{el}</li>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default Footer;