import React from "react";


type CategoriesProps = {
  value: number;
  onClicCat: (i: number) => void;
};
const categories = [
  "",
  "",
  "",
  "",
  "",
  "",
];

const Categories: React.FC<CategoriesProps> = React.memo(({ value, onClicCat }) => {
  return (
    <div className="categories">
      <ul>
        {categories.map((categor, index) => (
          <li
            key={index}
            onClick={() => onClicCat(index)}
            className={value === index ? "active" : ""}
          >
            {categor}
          </li>
        ))}
      </ul>
    </div>
  );
});

export default Categories;
