import React from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

import Categories from "../components/Categories";
import Sort from "../components/Sort";
import PizzaBlock from "../components/PizzaBlock/PizzaBlock";
import Skeleton from "../components/PizzaBlock/Skeleton";
import Pagination from "../components/Pagination";

import {
  selectFilters,
} from "../redux/filters/selectors";
import { useAppDispatch } from "../redux/store";
import { setCategryId } from "../redux/filters/slice";
import { selectPizza } from "../redux/pizzas/selectors";
import { fetchPizzas } from "../redux/pizzas/asyncActions";

const Home:React.FC = () => {

  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const isSearch = React.useRef(false);
  const isMounted = React.useRef(false);

  const { categoryId, sort, currentPage, searchValue } =
    useSelector(selectFilters);

  const { items, status } = useSelector(selectPizza);

  const onClicCat = React.useCallback((i:number) => {
    dispatch(setCategryId(i));
  }, [])

  //     1    url     
  // React.useEffect(() => {
  //   if (isMounted.current) {
  //     const queryString = qs.stringify({
  //       //    - 
  //       sortProp: sort.sortProp,
  //       categoryId,
  //       currentPage,
  //     });
  //     navigate(`?${queryString}`); //      ?    41  
  //   }
  //   isMounted.current = true;
  // }, [sort, categoryId, currentPage]);

  //      url     
  // React.useEffect(() => {
  //   if (window.location.search) {
  //     // -        
  //     const params = qs.parse(window.location.search.replace("?", "")) as unknown as SerachPizzaParams; //  ?   ,    
  //     const sort = list.find((obj) => obj.sortProp === params.sortBy);

  //     dispatch(
  //       setFilters({
  //         //   
  //         searchValue: params.search,
  //         categoryId: Number(params.category),
  //         currentPage: Number(params.currentPage),
  //         sort: sort || list[0],
  //       })
  //     );
  //     isSearch.current = true;
  //   }
  // }, []);

  //  1    
  React.useEffect(() => {
    getPizzas();

    isSearch.current = false;

    window.scrollTo(0, 0);
  }, [sort, categoryId, searchValue, currentPage]);

  const getPizzas = async () => {
    const category = categoryId > 0 ? `category=${categoryId}` : "";
    const sortBy = sort.sortProp.replace("-", "");
    const order = sort.sortProp.includes("-") ? "asc" : "desc";
    const search = searchValue ? `&search=${searchValue}` : "";

    dispatch(
      fetchPizzas({
        category,
        sortBy,
        order,
        search,
        currentPage: String(currentPage),
      })
    );

    window.scrollTo(0, 0);
  };

  const pizzaItems = items.map((obj:any) => (
      <PizzaBlock key={obj.id} {...obj} />
  ));

  const skeletonRev = [...new Array(6)].map((_, index) => (
    <Skeleton key={index} />
  ));

  return (
    <>
      <div className="content__top">
        <Categories value={categoryId} onClicCat={onClicCat} />
        <Sort value={sort}/>
      </div>
      <h2 className="content__title"> </h2>
      {status === "Error" ? (
        <div className="content__error-info">
          <h2>
              <span></span>
          </h2>
          <p>
             ,    ...
            <br />
               .
          </p>
        </div>
      ) : (
        <div className="content__items">
          {status === "Loading" ? skeletonRev : pizzaItems}
        </div>
      )}
      <Pagination currentPage={currentPage} />
    </>
  );
}
export default Home;